
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import NAVDATA from '../../utils/nav'

export default {
components: {
Tabbar,
TabbarItem
},
data () {
return {
selectItem: '1',
navData: NAVDATA
}
},
methods: {
getIp () {

},
navIndex () {
let routerPath = this.$route.path
let vm = this
this.navData.map(item => {
if (item.toActive.indexOf(routerPath) > -1) {
vm.selectItem = item.index
}
})
}
},
mounted () {
this.navIndex()
this.getIp()
}
}



